<template>
  <div class="view aboutus common">
    <el-image :style="{width:'100%'}" :src="style.src_pic"></el-image>
  </div>
</template>

<script>
import mymap from "./components/mymap"
import "@/styles/aboutus.css";
import "@/styles/common.css";

export default {
  name: "AboutUs",
  components: {
    "com-mymap": mymap
  },

  data() {
    return {
      style: {
        src_pic: require("@/assets/introduce2.png"),
        src_intro: require("@/assets/introduce1.png"),
        src_map: require("@/assets/introduce1.png")
      },

    };
  },
  mounted() { },
  methods: {}
};
</script>

<style scoped>
</style>

<style>
.aboutus .branches_content {
  color: #333333;
  white-space: pre-line;
  margin-bottom: -20px;
}

.aboutus .company_intro {
  text-align: center;
}
.aboutus .company_intro > p {
  text-align: justify;
}

.aboutus .company_intro .el-image {
  text-align: center;
}

.aboutus .contact_information {
  margin-top: 24px;
}
.aboutus .contact_information_img {
  margin-top: 24px;
}

.aboutus .contact_information_img .el-image {

}

.aboutus .contact_information > div > p {
  font-size: 24px;
  font-weight: bold;
}
.aboutus .contact_information > p {
}
.aboutus .contact_information .child1 {
}
.aboutus .contact_information .child2 {
  margin-top: 2.34vw;
}
.aboutus .body_bottom {
}
</style>



